"use client";

import React, { useState, useMemo } from "react";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import { Modal } from "@/components/ui/modal";
import { Table, TableBody, TableCell, TableHeader, TableRow } from "@/components/ui/table";
import Badge from "@/components/ui/badge/Badge";
import { Plus, Upload, ArrowLeft } from "lucide-react";

/* ================= TYPES ================= */
type Status = "Draft" | "Submitted" | "Approved" | "Rejected";

interface Reimbursement {
    id: number;
    title: string;
    category: string;
    amount: number;
    date: string;
    status: Status;
    receipts: string[];
}

/* ================= STATIC DATA ================= */
const initialData: Reimbursement[] = [
    {
        id: 1,
        title: "Taxi to client office",
        category: "Travel",
        amount: 1200,
        date: "2025-01-05",
        status: "Submitted",
        receipts: ["receipt1.pdf"],
    },
    {
        id: 2,
        title: "Hotel stay",
        category: "Accommodation",
        amount: 5400,
        date: "2025-01-10",
        status: "Approved",
        receipts: ["hotel.pdf"],
    },
];

/* ================= PAGE ================= */
export default function ReimbursementPage() {
    const [data, setData] = useState<Reimbursement[]>(initialData);
    const [view, setView] = useState<"list" | "detail" | "new">("list");
    const [selected, setSelected] = useState<Reimbursement | null>(null);

    const [statusFilter, setStatusFilter] = useState<Status | "All">("All");
    const [monthFilter, setMonthFilter] = useState("All");

    const [page, setPage] = useState(1);
    const pageSize = 5;

    /* ================= FILTERING ================= */
    const filtered = useMemo(() => {
        return data.filter((r) => {
            const month = r.date.slice(0, 7);
            return (
                (statusFilter === "All" || r.status === statusFilter) &&
                (monthFilter === "All" || month === monthFilter)
            );
        });
    }, [data, statusFilter, monthFilter]);

    const paginated = filtered.slice((page - 1) * pageSize, page * pageSize);
    const totalPages = Math.ceil(filtered.length / pageSize);

    /* ================= ACTIONS ================= */
    const openDetail = (r: Reimbursement) => {
        setSelected(r);
        setView("detail");
    };

    const openNew = () => {
        setSelected(null);
        setView("new");
    };

    const goBack = () => {
        setView("list");
    };

    /* ================= UI ================= */
    return (
        <div className="p-6">
            <PageBreadcrumb pageTitle="Reimbursements" />

            {/* ================= LIST VIEW ================= */}
            {view === "list" && (
                <>
                    {/* Header */}
                    <div className="flex justify-between items-center mb-4">
                        <div className="flex gap-3">
                            <select
                                className="border rounded px-3 py-2"
                                value={statusFilter}
                                onChange={(e) => setStatusFilter(e.target.value as any)}
                            >
                                <option value="All">All Status</option>
                                <option value="Draft">Draft</option>
                                <option value="Submitted">Submitted</option>
                                <option value="Approved">Approved</option>
                                <option value="Rejected">Rejected</option>
                            </select>

                            <select
                                className="border rounded px-3 py-2"
                                value={monthFilter}
                                onChange={(e) => setMonthFilter(e.target.value)}
                            >
                                <option value="All">All Months</option>
                                <option value="2025-01">Jan 2025</option>
                            </select>
                        </div>

                        <button
                            onClick={openNew}
                            className="flex items-center gap-2 bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                        >
                            <Plus size={16} /> New Reimbursement
                        </button>
                    </div>

                    {/* Table */}
                    <div className="rounded-xl border bg-white shadow">
                        <Table>
                            <TableHeader>
                                <TableRow>
                                    <TableCell isHeader>Title</TableCell>
                                    <TableCell isHeader>Category</TableCell>
                                    <TableCell isHeader>Date</TableCell>
                                    <TableCell isHeader className="text-right">Amount</TableCell>
                                    <TableCell isHeader>Status</TableCell>
                                </TableRow>
                            </TableHeader>

                            <TableBody>
                                {paginated.map((r) => (
                                    <TableRow key={r.id} className="hover:bg-gray-50">
                                        {/* ✔ FIXED: click on cell instead of TableRow */}
                                        <TableCell>
                                            <div
                                                role="button"
                                                tabIndex={0}
                                                onClick={() => openDetail(r)}
                                                onKeyDown={(e) => e.key === "Enter" && openDetail(r)}
                                                className="cursor-pointer font-medium text-blue-600 hover:underline"
                                            >
                                                {r.title}
                                            </div>
                                        </TableCell>
                                        <TableCell>{r.category}</TableCell>
                                        <TableCell>{r.date}</TableCell>
                                        <TableCell className="text-right">₹{r.amount}</TableCell>
                                        <TableCell>
                                            <Badge
                                                color={
                                                    r.status === "Approved"
                                                        ? "success"
                                                        : r.status === "Rejected"
                                                            ? "error"
                                                            : r.status === "Submitted"
                                                                ? "warning"
                                                                : "dark"
                                                }
                                            >
                                                {r.status}
                                            </Badge>
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>

                        {/* Pagination */}
                        {totalPages > 1 && (
                            <div className="flex justify-center gap-2 p-4">
                                <button disabled={page === 1} onClick={() => setPage(page - 1)} className="px-3 py-1 border rounded">
                                    Prev
                                </button>
                                {[...Array(totalPages)].map((_, i) => (
                                    <button
                                        key={i}
                                        onClick={() => setPage(i + 1)}
                                        className={`px-3 py-1 border rounded ${page === i + 1 ? "bg-blue-600 text-white" : ""
                                            }`}
                                    >
                                        {i + 1}
                                    </button>
                                ))}
                                <button
                                    disabled={page === totalPages}
                                    onClick={() => setPage(page + 1)}
                                    className="px-3 py-1 border rounded"
                                >
                                    Next
                                </button>
                            </div>
                        )}
                    </div>
                </>
            )}

            {/* ================= DETAIL VIEW ================= */}
            {view === "detail" && selected && (
                <div className="max-w-3xl bg-white border rounded-xl shadow p-6">
                    <button onClick={goBack} className="flex items-center gap-2 mb-4 text-blue-600">
                        <ArrowLeft size={16} /> Back to list
                    </button>

                    <h2 className="text-xl font-bold mb-4">{selected.title}</h2>

                    <div className="grid grid-cols-2 gap-4 text-sm">
                        <p><strong>Category:</strong> {selected.category}</p>
                        <p><strong>Date:</strong> {selected.date}</p>
                        <p><strong>Amount:</strong> ₹{selected.amount}</p>
                        <p><strong>Status:</strong> {selected.status}</p>
                    </div>

                    <div className="mt-4">
                        <h4 className="font-semibold mb-2">Receipts</h4>
                        {selected.receipts.map((r, i) => (
                            <div key={i} className="text-blue-600 underline">{r}</div>
                        ))}
                    </div>
                </div>
            )}

            {/* ================= NEW FORM ================= */}
            {view === "new" && (
                <div className="max-w-xl bg-white border rounded-xl shadow p-6">
                    <button onClick={goBack} className="flex items-center gap-2 mb-4 text-blue-600">
                        <ArrowLeft size={16} /> Back to list
                    </button>

                    <h2 className="text-xl font-bold mb-4">New Reimbursement</h2>

                    <div className="space-y-4">
                        <input className="w-full border px-3 py-2 rounded" placeholder="Title" />
                        <input className="w-full border px-3 py-2 rounded" placeholder="Category" />
                        <input type="number" className="w-full border px-3 py-2 rounded" placeholder="Amount" />
                        <input type="date" className="w-full border px-3 py-2 rounded" />

                        <label className="flex items-center gap-2 border rounded px-3 py-2 cursor-pointer">
                            <Upload size={16} /> Upload receipts
                            <input type="file" multiple hidden />
                        </label>

                        <button className="w-full bg-blue-600 text-white py-2 rounded hover:bg-blue-700">
                            Save as Draft
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
}
